@tailwind base;
@tailwind components;

@layer components {
  .wrapper {
    @apply animate-fade;
  }

  .arrowStyle {
    @apply rounded-full hover:bg-slate-200 flex justify-center items-center shadow-sm hover:cursor-pointer duration-100 ease-in-out;
  }

  .btn {
    @apply text-white bg-[#8a4af3] rounded-md shadow-md p-3 hover:scale-105 ease-in duration-100 cursor-pointer hover:bg-blue-500;
  }

  .styleIconTopProduct {
    @apply w-10 h-10 rounded-full cursor-pointer bg-white flex justify-center items-center hover:bg-[#894af3] hover:text-white hover:scale-110 ease-in duration-100;
  }
}

@tailwind utilities;
